import Slider from "react-slick";
import { useState, useEffect } from "react";
import "./assets/style/Learn.css"
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import CoureImage from './assets/img/C-image.jpg'
import { NavLink } from "react-router-dom";


const courseApi = 'https://jsonplaceholder.typicode.com/users'

const settings = {
    dots: false,
    infinite: true,
    autoplay: true,
    autoplaySpeed: 3000,
    // speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    // cssEase: "linear",
};

function TestSlide() {


    const [coures, setCoures] = useState([])

    useEffect(() => {
        fetch(courseApi)
            .then(function (response) {
                return response.json()
            })
            .then(function (response) {
                setCoures(response)
            })
    }, [])

    return (
        <div>
            <h2> Kha hc </h2>
            <Slider {...settings} >

                {coures.map(coure => (
                    <div key={coure.id}>
                        <div className="card">
                            <NavLink to="/code">
                                <img className="card-img-top" src={CoureImage} alt="hnh nh" />
                            </NavLink>

                            <div className="card-body">
                                <h5 className="card-title">{coure.name}</h5>
                                <p className="card-text">Kha hc lp trnh C cho ngi mi bt u. Kha hc ny s cung cp nhng kin thc c bn v l nn tng  bn i xa hn trn con ng lp trnh.</p>
                            </div>
                        </div>
                    </div>

                ))}

            </Slider>
        </div>
    );
}

export default TestSlide