import React from "react";
import { NavLink } from "react-router-dom";

import "../assets/style/Footer.css"

import companyLogo from '../assets/img/logo_transparent.png'
import facebookLogo from '../assets/img/facebook.png'
import twitterLogo from '../assets/img/twitter.png'
import instagramLogo from '../assets/img/instagram.png'
import youtubeLogo from '../assets/img/youtube.png'


function Footer() {
    return (
        <div className="Footer">

            <div className="Logo-Footer">
                <img src={companyLogo} alt="Logo" />
                <ul className="SocialNetwork">

                    <li>
                        <a href='/learning' aria-label="Trang facebook">
                            <img className="Facebook-logo" src={facebookLogo} alt="Facebook" />
                        </a>
                    </li>

                    <li>
                        <a href='/' aria-label="Trang instagram">
                            <img className="Instagram-Logo" src={instagramLogo} alt="Instagram" />
                        </a>
                    </li>

                    <li>
                        <a href='/' aria-label="Trang Twitter">
                            <img className="Twitter-Logo" src={twitterLogo} alt="Twitter" />
                        </a>
                    </li>

                    <li>
                        <a href='/' aria-label="Trang youtube">
                            <img className="Youtube-Logo" src={youtubeLogo} alt="Youtube" />
                        </a>
                    </li>

                </ul>
            </div>

            <div className="Right-footer"></div>

            <div className="Links">
                <h3>Lin kt</h3>
                <ul className="List">
                    <li><NavLink to="/learning">Hc tp</NavLink></li>
                    <li><a href="index.js">Luyn tp</a></li>
                    <li><a href="index.js">Cuc thi</a></li>
                    <li><a href="index.js">Tr chi</a></li>
                </ul>
            </div>

            <div className="Information">
                <h3>Thng tin</h3>
                <ul className="List">
                    <li><a href="index.js">Chia s</a></li>
                    <li><a href="index.js">v chng ti</a></li>
                    <li><a href="index.js">iu khon s dng</a></li>
                </ul>
            </div>

            <div className="Help">
                <h3>Tr gip</h3>
                <ul className="List">
                    <li><a href="index.js">H tr</a></li>
                    <li><a href="index.js">Tho lun</a></li>
                    <li><a href="index.js">Lin h vi chng ti</a></li>
                </ul>

            </div>

        </div>

    )
}

export default Footer;