import { NavLink, Route } from "react-router-dom";
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
// import { useState } from 'react';
import "../assets/style/Header.css"
import companyLogo from '../assets/img/logo_transparent.png'
import Content from "../Content";




function Header(props) {
   

    // const [show, setShow] = useState(false)

    const logoutAccount = () => {
        firebase.auth().signOut();
        alert("ng xut thnh cng");
        return(
            <Route path="/Hoc-code-UI" element={<Content />} />
        )
    }

    
    const isLogin = props.Check;
    console.log(isLogin)
    if (!isLogin) {
        return (
            <div id="header" >

                <NavLink className="logo" to="/Hoc-code-UI">
                    <img src={companyLogo} alt="Logo" />
                </NavLink>
                <div className="List-menu">
                    <div className="Menu-item active">
                        <NavLink to="/learning">Hc tp</NavLink>
                    </div>

                    <div className="Menu-item">
                        <NavLink to="/" >Luyn tp</NavLink>
                    </div>

                    <div className="Menu-item">
                        <NavLink to="/" className="a">Thi u</NavLink>
                    </div>

                    <div className="Menu-item">
                        <NavLink to="/" className="a">Th thch</NavLink>
                    </div>

                    <div className="Line"></div>
                </div>
                <div id="button">
                    <NavLink to='/login' className="btn_">NG NHP</NavLink>
                    <div className="btn_">ng k</div>
                </div>
            </div>
        )
    }

    return (
        <div id="header" >

            <NavLink className="logo" to="/Hoc-code-UI">
                <img src={companyLogo} alt="Logo" />
            </NavLink>
            <div className="List-menu">
                <div className="Menu-item active">
                    <NavLink to="/learning">Hc tp</NavLink>
                </div>

                <div className="Menu-item">
                    <NavLink to="/" >Luyn tp</NavLink>
                </div>

                <div className="Menu-item">
                    <NavLink to="/" className="a">Thi u</NavLink>
                </div>

                <div className="Menu-item">
                    <NavLink to="/" className="a">Th thch</NavLink>
                </div>

                <div className="Line"></div>
            </div>
            <div id="button">
                <div className="btn_" onClick={() => logoutAccount()} >NG Xut</div>
            </div>
        </div>

    )
}

export default Header;
