import useLookBodyScroll from "../../components/useLockBodyScroll";
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
import '../Login/Login.css';
// import { getNodeText } from "@testing-library/react";


function Login() {
    useLookBodyScroll();




    const uiConfig = {
        // Popup signin flow rather than redirect flow.
        signInFlow: 'redirect',
        signInSuccessUrl: '/home',
        // We will display Google and Facebook as auth providers.
        signInOptions: [
            firebase.auth.GoogleAuthProvider.PROVIDER_ID,
            firebase.auth.FacebookAuthProvider.PROVIDER_ID
        ],

    };

    return (
        <div id="Login">
            <div className="modal">
                <h1>ng nhp</h1>
                <input className="input" placeholder="Ti khon" />
                <input className="input" placeholder="Mt khu" />
                <button type="button" className="btnLogin">ng nhp</button>
                <span>Hoc ng nhp bng</span>
                <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
                <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
            </div>
        </div>
    )
}

export default Login;