import React from "react";
import { useState } from "react";

import TestSlide from './testSlide';
import "./assets/style/Learn.css"

function Learn() {

    const [name, setName] = useState('')

    const handerSearch = () => (
        alert(`Gi tr va nhp: ${name}`)
    )


    return (
        <div className='learnContent'>
            <div className='find'>
                <h2>T hc lp trnh trc tuyn. Hy bt u vi kho hc u tin ca bn!</h2>
                <div className='searchCourse'>
                    <input type='text' value={name} onChange={(e) => setName(e.target.value)} placeholder="Nhp ni dung tm kim" />
                    <p onClick={handerSearch} />
                </div>
            </div>

            <div className='mainContent'>
                <TestSlide/>
            </div>
        </div>
    )
}

export default Learn